<template>
  <v-container>
    <div class="text-begin pa-2 mb-2">افزودن محصول جدید</div>

    <v-row justify="center">
      <v-col order="12" cols="12" sm="7" md="7" class="text-center">
        <v-card class="pa-2 px-md-2 px-sm-2" outlined>
          <v-form>
            <v-row justify="center">
              <v-col cols="6">
                <CategorySelectComponent
                  v-on:on-select-category="onSelectCategory"
                  :category="category"
                ></CategorySelectComponent>
              </v-col>
              <v-col cols="6">
                <v-text-field
                  dense
                  v-model="title"
                  label="نام محصول"
                  :rules="[rules.required]"
                  outlined
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="6">
                <v-text-field
                  dense
                  v-model="realprice"
                  label="قیمت"
                  type="number"
                  :rules="[rules.required]"
                  outlined
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="6">
                <v-text-field
                  dense
                  v-model="price"
                  label="قیمت با تخفیف"
                  type="number"
                  :rules="[rules.required]"
                  outlined
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="6">
                <v-text-field
                  dense
                  v-model="amount"
                  label="موجودی"
                  placeholder="تعداد محصول موجود"
                  type="number"
                  :rules="[rules.required]"
                  outlined
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="6">
                <v-text-field
                  dense
                  v-model="cart_limit"
                  label="محدودیت تعداد خرید"
                  placeholder="محدودیت بسته پستی"
                  type="number"
                  :rules="[rules.required]"
                  outlined
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="6">
                <v-select
                  dense
                  v-model="selected_send_types"
                  :items="send_types"
                  item-text="send_type_persian"
                  item-value="id"
                  label="هزینه ارسال پستی"
                  :rules="[rules.required]"
                  multiple
                  outlined
                  required
                ></v-select>
              </v-col>
              <v-col cols="6">
                <v-text-field
                  dense
                  v-model="gaptime"
                  label="مدت تحویل"
                  placeholder="روز"
                  type="number"
                  prepend-icon="mdi-clock"
                  :rules="[rules.required]"
                  outlined
                  required
                ></v-text-field>
              </v-col>

              <v-col cols="12" sm="6" xs="8">
                <v-text-field
                  outlined
                  ref="deadline_date"
                  v-model="deadline_date"
                  :rules="[rules.required]"
                  label="مهلت فروش"
                  placeholder="انتخاب تاریخ اتمام مهلت فروش"
                  id="my-custom-input"
                  required
                ></v-text-field>
                <VuePersianDatetimePicker
                  v-model="deadline_date"
                  format="jYYYY-jMM-jDD"
                  element="my-custom-input"
                />
              </v-col>

              <v-col cols="12">
                <v-textarea
                  filled
                  rows="2"
                  counter="300"
                  v-model="description"
                  label="توضیحات"
                  :rules="[rules.required]"
                  required
                ></v-textarea>
              </v-col>

              <AlertComponent
              v-if="alert_msg"
              :alertMsg="alert_msg"
              :alertType="alert_type"
              >
              </AlertComponent>


              <v-col cols="6">
                <v-btn
                  block
                  depressed
                  :loading="loading"
                  :disabled="loading"
                  @click="addProduct"
                  color="primary"
                >
                  افزودن
                </v-btn>
              </v-col>
            </v-row>
          </v-form>
        </v-card>
      </v-col>

      <v-col cols="12" sm="5" md="5">
        <div v-sticky="{ isSticky: isNavSticky }">
          <v-card outlined class="pb-2">
            <v-card-title>عکس محصول</v-card-title>

            <v-row justify="center" class="pa-2">
              <ImageUploaderComponent
                v-for="i in inumber"
                :key="i"
                v-on:image-inputs="imageInput"
                v-on:remove-image-input="removeImageInput"
                :icon="i > 1 ? 'mdi-camera' : undefined"
              >
              </ImageUploaderComponent>
              <v-card
                flat
                outlined
                style="width: 100px"
                class="ma-1 py-8 px-2 d-flex align-center"
              >
                <v-row justify="center">
                  <v-btn icon class="text-center" @click="inumber++">
                    <v-icon>mdi-plus</v-icon>
                  </v-btn>
                </v-row>
              </v-card>
            </v-row>
          </v-card>
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import authHeader from "@/middleware/authHeader.js";
import { Sticky } from "@/middleware/sticky";

export default {
  directives: {
    Sticky,
  },
  data: () => ({
    category: {},
    categories: [],
    title: "",
    price: "",
    realprice: "",
    amount: "",
    gaptime: "",
    deadline_date: "",
    cart_limit: "",
    send_types: [],
    selected_send_types: "",
    description: "",
    images: [],
    isNavSticky: true,
    inumber: 1,
    loading: false,
    alert_msg: '',
    alert_type: '',
    rules: {
      required: (value) => !!value || "فیلد ضروری!",
      min: (v) => v.length >= 8 || "حداقل ۸ کاراکتر!",
      min4: (v) => v.length >= 4 || !isNaN(v) || "حداقل ۴ کاراکتر!",
      min11: (v) => v.length == 11 || isNaN(v) || "شماره موبایل ۱۱ رقمی است!",
      emailMatch: (v) =>
        !v.includes("@") ||
        /\S+@\S+\.\S+/.test(v) ||
        "ایمیل را درست وارد کنید!",
    },
  }),
  components: {
    ImageUploaderComponent: () =>
      import("@/components/commonComponents/ImageUploaderComponent.vue"),
    VuePersianDatetimePicker: () => import("vue-persian-datetime-picker"),
    AlertComponent: () => import("@/components/sdComponents/AlertComponent"),
    CategorySelectComponent: () =>
      import("@/components/commonComponents/CategorySelectComponent.vue"),
  },
  methods: {
    async fetchData () {
      try {
        const result = await this.$axios.post(
          "/graphql",
          {
            query: `query {
            categoryFindAll {
              id
              name
              persian_name
              categoryId {
                id
                name
                persian_name
              }
            }
          }`,
          },
          authHeader()
        );
        const scategories = result.data.data.categoryFindAll;
        this.categories = scategories;
      } catch (error) {
        console.log(error);
      }
    },
    async fetchSendTypes () {
      try {
        const result = await this.$axios.post(
          "/graphql",
          {
            query: `query {
            sendFindByCategory (query: {
              categoryId: "${this.category.id}"
            }) {
              id
              send_type
              send_type_persian
              send_price
            }
          }`,
          },
          authHeader()
        );
        this.send_types = result.data.data.sendFindByCategory;
      } catch (error) {
        console.log(error);
      }
    },
    imageInput(filename) {
      this.images.push(filename);
    },
    removeImageInput(filename) {
      this.images.filter((e) => {
        e != filename;
      });
    },
    onSelectCategory (item) {
      this.category = item;
    },
    async addProduct () {
      this.loading = true;
      this.instaLoginDialog = false;
      this.alert_msg= '';
      if (!this.price) this.price = this.realprice;
      try {
        const result = await this.$axios.post(
          "/graphql",
          {
            query: `mutation {
              productCreate (input: {
                title: "${this.title}"
                realprice: "${this.realprice}"
                price: "${this.price}"
                amount: "${this.amount}"
                gaptime: "${this.gaptime}"
                deadline_date: "${this.deadline_date}"
                cart_limit: ${this.cart_limit}
                send_types: ${JSON.stringify(this.selected_send_types)}
                thumbnail: "${this.images[0]}"
                description: "${this.description}"
                categoryId: "${this.category.id}"
                images: "${this.images.toString()}"
              }) {
                id
              }
            }`,
          },
          authHeader()
        );
        if (result.data.data.productCreate.id) {
          this.$router.push("/accounts/profile-products");
        }
      } catch (error) {
        console.log(error);
        this.alert_msg = 'لطفا همه موارد را به درستی کامل کنید'
        this.alert_type = 'error'
      }
      this.loading = false;
    },
  },
  mounted() {
    this.fetchData();
    this.fetchSendTypes();
  },
};
</script>

<style scoped>
</style>
